@import url('https://fonts.googleapis.com/css?family=Kanit:400');

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.new-center {
  line-height:4rem;
  text-align:center;
}

/* contract: Never use the default text things, always use these. foreground is 100, shadow 200-400 */

.new-text-amber {
  color: rgb(254 243 199);
  text-shadow: 0 1px rgb(253 230 138), 0 2px rgb(252 211 77), 0 3px rgb(252 211 77), 0 4px rgb(251 191 36), 0 5px rgb(251 191 36), 0 3px 4px rgba(0, 0, 0, 0.6);
}

.new-text-aqua {
  color: rgb(207 250 254);
  text-shadow: 0 1px rgb(165 243 252), 0 2px rgb(103 232 249), 0 3px rgb(103 232 249), 0 4px rgb(34 211 238), 0 5px rgb(34 211 238), 0 3px 4px rgba(0, 0, 0, 0.6);
}

.bottom-center {
  position: fixed;
  left: 46%;
  bottom: 20px;
  transform: translate(-50%, -50%);
  margin: 0 auto;
}

.bottom-center1 {
  position: fixed;
  left: 54%;
  bottom: 20px;
  transform: translate(-50%, -50%);
  margin: 0 auto;
}

.full-screen-centered {
  position: fixed; /* or absolute */
  top: 50%;
  left: 50%;
  /* bring your own prefixes */
  transform: translate(-50%, -50%);
}

.kanit {
  font-family: "Kanit", serif;
}